<template>
  <el-card class="countdown">
    <div class="countdown-body">
      <div class="name">{{exam.name}}</div>
      <div class="notice">
        <div v-if="!ended()">
          <div class="title"></div>
          <div class="content">
            <p></p>
            <p></p>
            <p>0</p>
            <p> Chrome  Firefox </p>
            <p></p>
            <p></p>
          </div>
        </div>
        <div v-else>
          <div class="title">
            
          </div>
        </div>
      </div>
      <div class="footer">
        <el-button type="primary" :disabled="waiting()" @click="handleClick">{{buttonText}}</el-button>
      </div>
    </div>
  </el-card>
</template>

<script>
  import Util from '../lib/util'

  export default {
    name: 'Countdown',

    props: {
      exam: Object
    },

    data () {
      return {
        time: ' : ',
        timeout: null
      }
    },

    created () {
      this.timeout = setInterval(() => {
        this.getTime()
      }, 1000)
    },

    computed: {
      buttonText: function () {
        return this.waiting() ? this.time : this.ended() ? 'ESS' : ''
      }
    },

    methods: {
      waiting: function () {
        return new Date() < new Date(this.exam.startTime)
      },
      ended: function () {
        return this.exam.end || new Date() > new Date(this.exam.endTime)
      },
      getTime: function () {
        let time = new Date(this.exam.startTime) - new Date()

        let day = Math.floor(time / 1000 / 60 / 60 / 24)

        this.time = ' : ' + day + Util.formatTime(new Date(time), 'hms')
      },
      handleClick: function () {
        if (this.ended()) {
          this.$router.push('/index')
        } else {
          clearTimeout(this.timeout)
          this.$emit('onStartExam')
        }
      }
    },

    destroyed () {
      clearTimeout(this.timeout)
    }
  }
</script>

<style scoped>
  .name {
    color: #409EFF;
    margin: 20px;
    text-align: left;
    font-size: 2em;
    font-weight: 200;
  }

  .notice {
    margin: 0 20px;
    background: #f4f4f4;
    border-radius: 4px;
  }

  .title {
    padding: 40px;
  }

  .content {
    padding: 0 20px 20px;
    text-align: left;
  }

  .footer {
    padding: 40px 0 20px;
    justify-self: end;
  }
</style>



